body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.loading-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.loading-image {
  width: 150px;
  height: auto;
}

.bounce {
  animation: bounce 1.5s infinite;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

.content {
  width: 100%;
}

.header {
  display: flex;
  align-items: center;
  padding: 20px;
  background-color: #282c34;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
}

.logo {
  width: 50px;
  height: auto;
  margin-right: 20px;
  transition: all 0.5s ease;
}

.header-title {
  font-size: 1.5em;
}

.intro {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f0f0f0;
}

.intro-text {
  font-size: 3em;
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: transform 1s, opacity 1s;
}

.intro-subtext {
  font-size: 1.5em;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  animation: typing 3s steps(30, end), blink-caret 0.75s step-end infinite;
  animation-delay: 1s;
  animation-fill-mode: forwards;
  white-space: nowrap;
  overflow: hidden;
  border-right: 0.15em solid #000;
}

@keyframes typing {
  from {
    width: 0;
    opacity: 1;
  }
  to {
    width: 12em;
    opacity: 1;
  }
}

@keyframes blink-caret {
  from, to {
    border-color: transparent;
  }
  50% {
    border-color: black;
  }
}

.main-content {
  padding-top: 100px; /* To account for fixed header */
  padding: 20px;
  opacity: 0;
  transition: opacity 1s;
}

.main-content.hidden {
  display: none;
}

.main-content:not(.hidden) {
  opacity: 1;
  display: block;
}

.section {
  margin: 20px 0;
}

.slick-slider {
  max-width: 800px; /* Set the maximum width for the carousel */
  margin: 0 auto; /* Center the carousel */
}

.slick-slide {
  text-align: center;
}

.slick-dots {
  bottom: -30px;
}

@keyframes flyAway {
  0% {
    transform: translate(-50%, -50%);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -150%);
    opacity: 0;
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
